.about{
    background-color: #FFFFFF;
    &__container{
        max-width: 1170px;
        margin: 0 auto;
    }
    &__wrapper{
        padding-top: 42px;
        padding-bottom: 108px;
    }
    &__title{
        width: 100%;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        h3{
            font-family: @KaushanScript;
            font-weight: 400;
            font-size: 30px;
            line-height: 44px;            
        }
    }
    &__name{
        width: 100%;
        h2{
            height: 80px;
            font-weight: 900;
            font-size: 36px;
            line-height: 42px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .pure-line{
            margin-top: 24px;
            margin-bottom: 44px;
        }
    }
    &__discription{
        width: 100%;
        margin-bottom: 80px;
        p{
            text-align: center;            
            font-weight: 400;
            font-size: 18px;
            line-height: 21px;
        }
    }
    &__cards{
        display: grid;
        grid-template: repeat( 1, 100%) / repeat(3, minmax(300px,370px)) ;
        gap: 30px;
        
    }
    &__card{
        transition: 0.3s ease;
    }
    &__card:hover{
        transform: translateY(-20px);
        box-shadow: 10px 10px 0px #95E1D3;
        transition: 0.5s;
    }
}